"use strict";
import {
	login,
	getUserInfo,
	getMobile,
	getQRcode,
	saveUserMsg,
	getUserState,
	getUserTikuMsg,
	changeTiku,
	coachGetMsg,
	feedBack,
	saveStatus,
	setPersonMsg,
	getPersonMsg,
	getPhone,
} from '@/api/user.js'
import db from "@/system/core/nardy.db.js";
const state = {
	name:null,
	phone:null,
	userInfo: {},
	userState: {},
	userMsgSub: {},
	order_count: {},
	tikuType: '',
	TikuRes: {},
	QRcode: {}, //
	feedBackRes: {}, //
	setPersonRes: null, //
	getPersonRes: null,
	getPersonForm: {
		nickName: '',
		phone: '',
		photo: '',
	}, //
	
	getPhoneRes:null//
};

const getters = {

};

const actions = {
	async getUserInfo({
		commit
	}, params) {
		// this.$win.loading("")
		let res = await getUserInfo(params);
		if (res && res.code == 0) {
			
			commit("set_user", res.user)
			commit('get_tikuMsg', res.user)
			// this.$win.close("l")
			return res
		} else {
			this.$win.nlog(res)
		}
	},
	async getQRcode({
		commit
	}, params) {
		this.$win.loading('')
		let res = await getQRcode(params)
		this.$win.close("l")
		if (res && res.code == 0) {
			commit('createQRcode', res)
			// this.$win.close("l")
		} else {
			this.$win.nlog(res)
		}

	},
	//
	async saveUserMsg({
		commit
	}, params) {
		this.$win.nlog('...')
		let res = await saveUserMsg(params)
		if (res.code == 0) {
			this.$win.close("l")
			this.$win.nlog('')
			commit('saveMsg', res)
		} else {
			this.$win.nlog('')
		}
	},
	//
	async getUserState({
		commit
	}, params) {
		let res = await getUserState(params)
		console.log(res);
		commit('userState', res)
	},
	//
	async getUserTikuMsg({
		commit
	}, params) {
		let res = await getUserTikuMsg(params)
		commit('get_tikuMsg', res)
	},
	//
	async changeTiku({
		commit
	}, params) {
		let res = await changeTiku(params)
		commit('changeTiku', res)
	},
	//
	async feedBack({
		commit
	}, params) {
		let res = await feedBack(params)
		commit('isSubSucceed', res)
	},
	//
	async saveStatus({
		commit
	}, params) {
		let res = await saveStatus(params)
		commit('save_Status', res)
	},
	//
	async setPerson({
		commit
	}, params) {
		let res = await setPersonMsg(params)
		// console.log('',res);
		if (res.code == 0) {
			commit('setPerson_Msg', res)
			this.$win.nlog('')
			if (params.new == 1) {
				return
			} else {
				setTimeout(() => {
					this.$win.back()
				}, 300)
			}

		} else {
			this.$win.nlog(res.code + res.msg)
		}
	},
	//
	async getPerson({
		commit
	}, params) {
		this.$win.loading('')
		let res = await getPersonMsg(params)
		this.$win.close('l')
		// console.log('',res);
		if (res.code == 0) {
			this.$db.set('identity',res.user.identity)
			commit('getPerson_Msg', res)
		}
	},
	async getPhone({commit},params) {
		let res=await getPhone(params)
		if(res.code==0){
			commit('getPhone_Res',res)
		}else{
			this.$win.nlog(res.msg)
		}
	}

};

const mutations = {
	set_user(state, data) {
		state.userInfo = data
	},
	// set_token(state,data){
	// 	state.token = data
	// },
	// order_count(state,data){
	// 	state.order_count = data
	// },
	createQRcode(state, data) {
		console.log('', data)
		state.QRcode = data
	},
	saveMsg(state, data) {
		console.log(data);
		state.userMsgSub = data
	},
	userState(state, data) {
		console.log("", data);
		state.userState = data.resultMap
	},
	get_tikuMsg(state, data) {
		console.log('', data)
		state.tikuType = data.address + '-' + data.driverType
	},
	isSubSucceed(state, data) {
		console.log('', data)
		state.feedBackRes = data
		if (data.code == 0) {
			if (data.msg == 'success')
				this.$win.nlog('')
			else
				this.$win.nlog('')
		} else {
			this.$win.nlog('')
		}
	},
	changeTiku(state, data) {
		console.log('', data);
		if (data.code == 0) {
			this.$win.nlog('')
			// console.log(6);
			setTimeout(() => {
				this.$win.back()
			}, 300)
			state.TikuRes = data;
		} else {
			this.$win.nlog(data.msg)
			console.log(data.code, '');
			this.$win.nlog('')
		}
	},
	save_Status(state, data) {
		console.log('', data);
		this.$win.nlog(data.msg)
		if (data.code == 0) {
			setTimeout(() => {
				this.$win.back()
			}, 300)
		}
	},
	getPerson_Msg(state, data) {
		state.getPersonRes = data.user
		state.name=data.user.name
		state.phone=data.user.phone
		state.getPersonForm.nickName = data.user.nickName
		state.getPersonForm.phone = data.user.phone
		state.getPersonForm.photo = data.user.photo
	},
	setPerson_Msg(state, data) {
		state.setPersonRes = data
	},
	
	//
	getPhone_Res(state,data){
		state.getPhoneRes=data.phone
	}
};

export default {
	namespaced: true, //falseactionmutationgetter trueaction,muationgetter
	state,
	getters,
	actions,
	mutations
}