///
export function collect(params) {
 
 if(!params.bool){
  //
  let url = config.apis.COLLECT + params.course
  //course(1||4)
  return http.post(url, params)
 }else{
  //
  let url=config.apis.DELETECOLLECT+params.course+'/'+params.id+'/'+params.userId
  return http.get(url,params)
 }
 
}

