//
"use strict";
import {
	allOrderList,
	payOrderList,
	nopayOrderList,
	cancelOrder,
} from '@/api/order.js'

const state = {
	orderList: [],
	allOrder: [],
	payOrder: [],
	nopayOrder: [],
	pageSize: 10,
	pageIndex: 1,
}

const getters = {

}


const actions = {
	//
	async allOrderList({
		commit
	}, params) {
		this.$win.loading('')
		let res = await allOrderList(params)
		this.$win.close('l')
		if (res && res.code === 0){
			if (params.page == 1) {
				commit('allOrder', res)
			} else {
				commit('pushPage', res)
			}
		}
		

	},
	//
	async payOrderList({
		commit
	}, params) {
		this.$win.loading('')
		let res = await payOrderList(params)
		this.$win.close('l')
		// console.log('', params.page);
		if (res && res.code === 0){
			if (params.page == 1) {
				commit('payOrder', res)
			} else {
				commit('pushPage', res)
			}
		}
		

	},
	//
	async nopayOrderList({
		commit
	}, params) {
		this.$win.loading('')
		let res = await nopayOrderList(params)
		this.$win.close('l')
		if (res && res.code === 0) {
			if (params.page == 1) {
				commit('nopayOrder', res)
			} else {
				commit('pushPage', res)
				// console.log('>?>');
			}
		}

	},

	//
	async cancelOrder({
		commit
	}, params) {
		if(params.i===2){
			this.$win.loading('')
			let res = await cancelOrder(params)
			this.$win.close('l')
			if (res.code == 0) {
				// this.$win.back()
				this.$win.nlog('')
			}
		}else{
			this.$win.loading('')
			let res = await cancelOrder(params)
			this.$win.close('l')
			if (res.code == 0) {
				this.$win.back()
				this.$win.nlog('')
			}
		}
		
		// commit('',res)
	}
}


const mutations = {
	//
	allOrder(state, data) {
		// console.log('', data);
		// state.allOrder=data.page.list
		state.orderList = data.page.list
	},
	//
	payOrder(state, data) {
		console.log('', data);
		// state.payOrder=data.page.list
		state.orderList = data.page.list
	},
	//
	nopayOrder(state, data) {
		// console.log('', data);
		// state.nopayOrder=data.page.list
		state.orderList = data.page.list
	},
	//
	pushPage(state, data) {
		if (data.code == 0) {
			if (data.page.list.length == 0) {
				this.$win.nlog('')
			} else {
				state.orderList = state.orderList.concat(data.page.list)
			}

		} else {
			this.$win.nlog('')
		}


	},
	//
	clearPage(state) {

		state.pageIndex = 1
	}
}


export default {
	namespaced: true, //falseactionmutationgetter trueaction,muationgetter
	state,
	getters,
	actions,
	mutations
}